.section {
  padding: 72px 10px;
  margin: 0 auto;
  position: relative;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.section .first-section-block {
  padding: 72px 10px;
  width: 100%;
  height: 100%;
}

.first-section-block_orange {
  background-color: #f5b050;
}

.first-section-block-layout {
  max-width: 1140px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.first-section-content-container {
  position: relative;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  color: white;
  height: 300px;
}
.first-section-content-container__logo_image {
  max-width: 100%;
}
.first-section-content-container__content_text {
  font-size: 1.5rem;
  font-weight: bold;
  flex-wrap: nowrap;
}
.first-section-content-container__button {
  color: white;
  background-color: black;
  font-size: 18px;
  border-radius: 40px;
  border: 2px solid transparent;
  text-align: center;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  transition: 0.15s ease-in-out;
  box-shadow: 0 6px 6px 0 rgba(0, 0, 0, 0.24);
  display: block;
  padding: 16px 39px;
}

.first-section-picture-container__image {
  background-image: url(./assets/images/heroImage@2x.png);
  background-size: cover;
  background-repeat: no-repeat;
  width: 678px;
  height: 484px;
  flex-shrink: 1;
}

@media (max-width: 1764px) {
  .first-section-block-layout {
    justify-content: center;
  }

  .first-section-content-container__content_text {
    font-size: 1.75rem;
  }

  .first-section-picture-container {
    width: 70%;
  }

  .first-section-picture-container__image {
    background-size: 90%;
  }
}
@media (max-width: 1200px) {
  .first-section-picture-container__image {
    background-image: url(./assets/images/heroImage.png);
    /*background-size: 70%;*/
    width: 90%;
  }
}
@media (max-width: 768px) {
  .first-section-block_orange {
    background-size: contain;
  }

  .first-section-block-layout {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-shrink: 0;
  }

  .first-section-content-container__logo_image {
    display: none;
  }
  .first-section-content-container__content_text {
    font-size: 30px;
    text-align: center;
  }

  .first-section-picture-container {
    width: 100%;
  }
  .first-section-picture-container__image {
    width: 100%;
    background-image: url(./assets/images/heroImage@2x.png);
  }
}
